.shower.full .slide .shout.grow,
.shower.full .slide .shout.shrink {
  transition: none !important;
}
.shower.full {
  background-color: #111;
}
.shower.list .slide.active {
  box-shadow: 0 0 0 1px #6f12f4, 0 0 0 20px #6f12f4, 0 20px 50px rgba(0,0,0,0.3);
}
.slide::after {
  color: #1b1b1b;
  background: none;
  height: 75px;
  right: 20px;
}
.slide::before {
  position: absolute;
  top: 75px;
  right: 100px;
  height: 50px;
}
.slide {
  padding-top: 60px;
  padding-left: 60px;
  padding-right: 60px;
}
.slide a {
  color: #6f12f4;
}
.slide.no-top {
  padding-top: 0;
}
.slide.center {
  text-align: center;
}
.slide.red {
  background-color: #1b1b1b;
}
.slide.red h2 {
  color: #fff;
}
.slide h2 {
  color: #1b1b1b;
  font-family: 'PT Sans', sans-serif;
  background: #6f12f4;
  background: -webkit-linear-gradient(90deg, #6f12f4 0%, #8813fc 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.slide h2.square {
  color: #fff;
  font-family: 'PT Sans', sans-serif;
  margin-top: -30px;
  text-align: center;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  font-size: 5em;
}
.slide h2.square::before {
  content: '';
  display: block;
  background-color: #2d2d2d;
  width: 550px;
  height: 400px;
  position: absolute;
  top: calc(50% - 200px);
  left: calc(50% - 275px);
  z-index: -10;
  border: 5px solid #fff;
  opacity: 0.25;
}
.slide h2.ask {
  margin-top: 150px;
  font-size: 72px;
  width: 70%;
}
.slide.captain h2 {
  margin: 0;
  margin-top: -100px;
  width: 100%;
  padding: 30px 0;
  border-radius: 0;
  position: relative;
  font-size: 60px;
  height: 180px;
  align-items: center;
  justify-content: center;
  display: flex;
  background-color: #1b1b1b;
  color: #fff;
}
.slide.captain h2::after {
  content: '';
  background-color: #1b1b1b;
  width: 50px;
  height: 50px;
  display: block;
  position: absolute;
  bottom: -25px;
  transform: rotate(-10deg) skewY(50deg);
  right: 125px;
  z-index: -1;
}
.slide .captain-text {
  position: absolute;
  bottom: 15%;
  text-align: center;
  width: 100%;
  left: 0;
  font-family: 'PT Sans', sans-serif;
  font-size: 2em;
}
.slide .subtitle {
  font-family: 'PT Sans', sans-serif;
  background-color: #ddd;
  width: auto;
  display: inline-block;
  font-size: 50px !important;
  padding: 5px 20px;
  line-height: 60px;
}
.slide .label {
  background: #6f12f4;
  background: linear-gradient(90deg, #6f12f4 0%, #8813fc 100%);
  color: #fff;
  padding: 10px;
  display: inline-block;
  -webkit-background-clip: unset;
  -webkit-text-fill-color: #fff;
}
.hightlight,
.important,
.alert {
  border-radius: 0.2em;
  padding: 0.1em 0.3em;
}
.hightlight {
  background: #fafaa2;
}
.hightlight * {
  color: #000 !important;
}
.important {
  background: #a11212;
  color: #fff;
}
.important * {
  color: #fff !important;
}
.alert {
  background: #d10a0a;
  color: #fff;
}
.alert * {
  color: #fff !important;
}
.comment {
  color: #999;
}
.progress {
  border-color: #6f12f4 rgba(0,0,0,0) #6f12f4 #6f12f4;
}
.tag {
  color: #ad1531;
}
.attr {
  color: #4f7302;
}
.slide p.center {
  text-align: center;
}
.slide p.center img {
  max-width: 100%;
}
.slide p.quote {
  text-align: center;
  font-style: italic;
}
.slide.no-page::after {
  visibility: hidden;
}
.slide .copyright {
  position: absolute;
  right: 10px;
  bottom: 20px;
  transform: translateX(100%) rotate(-90deg);
  transform-origin: 0 100%;
  font-size: 12px;
  color: #6f12f4;
}
.slide .copyright.white {
  color: #fff;
}
blockquote {
  font-size: 35px;
  line-height: 1.4;
}
figcaption {
  opacity: 0.5;
  position: relative;
  line-height: 1.6;
  margin-top: 40px !important;
}
.slide ul>li:lang(ru)::before {
  color: #6f12f4;
  content: '◼︎';
}
.slide ol li,
.slide ul li {
  padding-left: 32px;
}
.slide li>ul>li {
  color: #444;
  line-height: 30px;
  font-size: 20px;
}
.strike {
  text-decoration: line-through;
}
.anti-mudak {
  color: #888;
  position: absolute;
  top: 80px;
  left: 60px;
  font-size: 0.75em;
}
.link {
  position: relative;
  margin-left: 1.8em;
  line-height: 1.3em;
}
.link::before {
  left: -1.8em;
  position: absolute;
  top: 0.1em;
  display: inline-block;
  width: 1.5em;
  height: 1.4em;
  content: '';
  background-repeat: no-repeat;
  background-size: contain;
}
.link-github::before {
  background-image: url("pictures/github-logo.svg");
}
.hashtag {
  font-size: 32px;
  opacity: 0.6;
}
